*{
    margin:0 0 0 0;
    background-color: #D4F1F4;
}

#greenbox{
    background-color: #75E6DA;
    height:250px;
    margin-top:0;
    text-align: center;
}
h1{
    align-items: center;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size:50px;
    text-align: center;
    color:#05445E;
    background-color : #75E6DA;
}
.input{
    background-color: white;
    color:black;
    width:25%;
    height:50px;
    margin-top:60px;
    border-radius: 20px;
    border:2px solid black;
    font-size: large;
}
#button{
    background-color: white;
    width:120px;
    color:#75E6DA;
    border-radius: 20px;
    border:2px solid black;
    height:50px;
    margin-top:60px;
    /* text-align: center; */
    font-size: large;
}
#image{
    text-align: center;
    margin-top:20px;
}
#download{
    background-color: black;
    color:#75E6DA;
    width:12em;
    height:3em;
    margin-bottom:50px;
}
#imageheading{
    color:#189AB4;
    font-size:50px;
}
img{
    color:blue;
    background-color: #D4F1F4;
}